@use "base/variables" as v;
@use "base/mixins" as m;

.about-festival {
  padding: 5rem 0;
  @include m.tablet {
    @include m.grid(2, 5rem);
  }
}

.festival-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 4rem;
  @include m.tablet {
    margin-top: 0;
  }
  
  h2 {
    margin-bottom: 3rem;
  }
  .date {
    color: v.$pink;
    font-weight: 700;
  }
}
