@use "base/variables" as v;
@use "base/mixins" as m;

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;

  .overlay {
    position: absolute;
    background: rgb(113, 5, 194);
    background: linear-gradient(
      270deg,
      rgba(113, 5, 194, 0.44859950816264005) 0%,
      rgba(200, 2, 116, 0.32815132889093135) 100%
    );
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  video {
    width: auto;
    display: block;
    @include m.desktop {
        width: 100%;
    }
  }
}

.video-content {
  z-index: 1;

  h2 {
    color: v.$white;
    font-size: 7rem;
    margin: 0;
  }

  p {
    background-color: v.$pink;
    display: inline-block;
    padding: 1rem 2rem;
    color: v.$white;
    font-weight: 700;
    font-size: 2.4rem;
  }
}
