@use "base/variables" as v;
@use "base/mixins" as m;

.gallery {
  padding: 5rem 0;

  h3 {
    text-align: center;
  }
}

.gallery-images {
  @include m.reset-list;
  @include m.grid(2, 2rem);
  @include m.phone {
    @include m.grid(3, 2rem);
  }
  @include m.tablet {
    @include m.grid(4, 2rem);
  }
  @include m.desktop {
    @include m.grid(6, 2rem);
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;

  display: grid;
  place-items: center;

  background-color: rgb(0 0 0 / 0.5);

  picture {
    display: block;
    max-width: 70rem;
  }
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: v.$white;
  color: v.$black;
  display: grid;
  place-items: center;

  font-weight: 600;

  &:hover{
    cursor: pointer;
  }
}
